<svelte:head>
  { @html atom }
</svelte:head>

<Header name="Start"
        legend="How to install and use Spaper" />

<h4>Import papercss</h4>

Add a link to the CDN into your HTML file:

<Highlight class="border padding-small"
           language={xml}
           code='<link rel="stylesheet" href="https://unpkg.com/papercss@1.8.3/dist/paper.min.css">' />

Or use NPM:

<Highlight class="border padding-small"
           code="npm i papercss" />
<Highlight class="border padding-small"
           language={xml}
           code={cssImportCode} />

<h4>Install spaper</h4>

<Highlight class="border padding-small"
           code="npm i spaper" />

<h4>Usage</h4>

<Highlight class="border padding-small"
           language={xml}
           code={usageCode} />

<script lang="ts">
import Header from '../docs/Header.svelte';
import Highlight from "svelte-highlight";
import xml from "svelte-highlight/src/languages/xml";
import atom from "svelte-highlight/src/styles/atom-one-dark-reasonable";

const cssImportCode: string = `
<script>
  import 'papercss/dist/paper.min.css'
<\/script>`;

const usageCode: string = `
<script>
  import { Button } from 'spaper';
  // or
  import Alert from 'spaper/components/Alert.svelte';
<\/script>

<Alert>...</Alert>
<Button>Click me</Button>`;
</script>